        },
        formatDate(dateObj) {
            if (!dateObj) return null;
            const d = new Date(dateObj);
            const year = d.getFullYear();
            const month = String(d.getMonth() + 1).padStart(2, '0');
            const day = String(d.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        },
        databaseConditionText() {
            return this.form.conditions.map((row, index) => {
                const relation = index > 0 ? ` ${row.relation || 'AND'} ` : '';
                const open = row.openingBracket || '';
                const close = row.closingBracket || '';
                const field = this.fieldSqlConditionMap[row.field] || row.field || '';

                let operator = '';
                let operatorDisplay = '';
                if (row.operatorList && Array.isArray(row.operatorList)) {
                    const op = row.operatorList.find(o => o.OPERATOR_ID === row.operator);
                    operator = op ? (op.OPERATOR_SQL_NAME || op.OPERATOR_EXPRESSION) : row.operator;
                    operatorDisplay = op ? (op.OPERATOR_DISPLAY_NAME || '') : '';
                } else {
                    operator = row.operator;
                    operatorDisplay = row.operatorDisplay || '';
                }

                operator = (operator || '').toLowerCase();

                // normalize operatorDisplay for safe comparison
                const displayCheck = (operatorDisplay || '').trim().toLowerCase();

                //  handle "equal to / not equal to one of multiple possible values"
                if (
                    displayCheck === 'not equal to one of multiple possible values' ||
                    displayCheck === 'equal to one of multiple possible values'
                ) {
                    const values = Array.isArray(row.value) ? row.value : [row.value];

                    const selectedValues = values.map(v => {
                        const val = typeof v === "object" && v !== null ? v.value : v;
                        const match = row.valueOptions?.find(opt => opt.value === val);
                        const finalVal = match ? match.value : val;

                        //  check JS type, quote only if not a pure number
                        return isNaN(finalVal) ? `'${finalVal}'` : finalVal;
                    });

                    const value = selectedValues.join(', ');
                    return `${relation}${open}${field} ${operator.toUpperCase()} (${value})${close}`;
                }


                // special cases
                if (operator === 'is week end' || operator === 'is working day') {
                    const days = (row.daysOfWeek || '').split(',').filter(Boolean).join(',');
                    const extra = `DATEPART(DW, ${field}) IN (${days})`;
                    return `${relation}${open}${extra}${close}`;
                }
                if (operator === 'is lowest') {
                    return `${relation}${open}${field} IS LOWEST${close}`;
                }
                if (operator === 'is highest') {
                    return `${relation}${open}${field} IS HIGHEST${close}`;
                }
                if (operator === 'is not lowest') {
                    return `${relation}${open}${field} IS NOT LOWEST${close}`;
                }
                if (operator === 'is not highest') {
                    return `${relation}${open}${field} IS NOT HIGHEST${close}`;
                }

                // DateDiff operators
                if (['minutesdiff','hoursdiff','daydiff','monthdiff','yeardiff'].includes(operator)) {
                    const unitMap = {
                        minutesdiff: 'minute',
                        hoursdiff: 'hour',
                        daydiff: 'day',
                        monthdiff: 'month',
                        yeardiff: 'year'
                    };
                    const diffUnit = unitMap[operator];
                    const compareField = this.fieldSqlConditionMap[row.additionalField] || row.additionalField;
                    const opSql = row.additionalOperator || '='; 
                    return `${relation}${open}DATEDIFF(${diffUnit}, ${field}, ${compareField}) ${opSql} ${row.value}${close}`;
                }

                // HAS WITH operator
                if (operator === 'has with') {
                    const withField = this.fieldSqlConditionMap[row.hasWithField] || row.hasWithField;
                    const withOp = row.hasWithOperator || '=';
                    return `${relation}${open}${field} HAS WITH ${withField} ${withOp} '${row.value}'${close}`;
                }
                // BETWEEN / DATERANGE special case
                if ((operator === 'between' || operator === 'daterange') && row.valueTo) {
                    // --- TIME / DATETIME ---
                    if (row.sqlType === 'time' || row.sqlType === 'datetime') {
                        const fromVal = row.value ? `'${row.value}'` : "''";
                        const toVal = row.valueTo ? `'${row.valueTo}'` : "''";

                        return `${relation}${open}(CONVERT(time, ${field}) >= ${fromVal} AND CONVERT(time, ${field}) <= ${toVal})${close}`;
                    }
                    // --- DATE ---
                    else if (row.sqlType === 'date') {
                        const dateVal = row.value instanceof Date ? row.value : new Date(row.value);
                        const dateValTo = row.valueTo instanceof Date ? row.valueTo : new Date(row.valueTo);

                        const fromDate = this.formatDate(dateVal);
                        const toDate = this.formatDate(dateValTo);

                        return `${relation}${open}${field} BETWEEN '${fromDate}' AND '${toDate}'${close}`;
                    }
                    // --- Default numeric/string ---
                    else {
                        return `${relation}${open}${field} BETWEEN ${row.value} AND ${row.valueTo}${close}`;
                    }
                }
                let value = '';
                if (row.sqlType === 'nvarchar' || row.sqlType === 'string') {
                    if (operator.includes('starts with')) {
                        operator = 'LIKE';
                        value = `'${row.value}%'`;
                    } 
                    else if (operator.includes('ends with')) {
                        operator = 'LIKE';
                        value = `'%${row.value}'`;
                    } 
                    else if (operator.includes('like')) {
                        operator = 'LIKE';
                        value = `'%${row.value}%'`;
                    } 
                    else if (operator.includes('in')) {
                        operator = 'IN';
                        const arr = Array.isArray(row.value) ? row.value : (row.value || '').split(',');
                        value = `(${arr.map(v => `'${v.trim()}'`).join(',')})`;
                    } else {
                        value = `'${row.value}'`;
                    }
                } 
                else if (row.sqlType === 'date' && row.value) {
                    const dateVal = row.value instanceof Date ? row.value : new Date(row.value);
                    value = `'${this.formatDate(dateVal)}'`;
                }
                else if (row.sqlType === 'bool') {
                    value = row.value ? 1 : 0;
                } 
                else if(displayCheck === 'equal to' && row.sqlType ==='select'){
                    if (typeof row.value === 'string') {
                        value = `'${row.value}'` || '';
                    }
                }
                else {
                    value = row.value || '';
                }
                return `${relation}${open}${field} ${operator.toUpperCase()} ${value}${close}`;
            }).join('');
        },
        CloneService() {
            var vm = this;
            vm.isCloneModel = true;
        },
        CloseCloneModel() {
            this.isCloneModel = false;
        },
        CloseRuleModel()
        {
            debugger;
            this.openRuleMode=false;  
        },
        RuleModel()
       {
            // Sync conditionBlocks to form.conditions before saving
            this.syncFormConditions();
            debugger;
            if (Number(this.conditionId) > 0 
                && Array.isArray(this.getRuleConditions) 
                && this.getRuleConditions.length > 0
                && this.reqFrom === "fromBillingService") {
                this.openRuleMode = true;
            } else {
                this.saveAddRule();
            }
        },
        async saveAddRule() {
            var vm = this;
            this.showError = true;
            if (!this.conditionName) {
                return;
            }
            if (vm.conditionId != 0) {
                this.form.conditionId = vm.conditionId;
            }
            if(this.form.rateToggle == true){
                this.form.isLeaf =true;
            }
            const conditionData = this.conditionText;
            const sqlDatabasecondition = this.databaseConditionText();
            if (Array.isArray(this.form.conditions)) {
                this.form.conditions.forEach(condition => {
                    if (condition.sqlType === 'date') {
                        if (condition.value) {
                            condition.value = this.formatDate(condition.value);
                        }
                        if (condition.valueTo) {
                            condition.valueTo = this.formatDate(condition.valueTo);
                        }
                    }
                    if (condition?.operator && Array.isArray(condition.value) && condition.value.length > 0) {
                        const operatorId = condition.operator;
                        const matchedOperator = condition.operatorList.find(op => op.OPERATOR_ID === operatorId);
                        if (matchedOperator && (matchedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase() === "equal to one of multiple possible values" || matchedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase() === "Not Equal to one of multiple possible values")) {
                            condition.value = condition.value.map(v => v.value).join(',');
                        }
                    }
                });
            }
            const payload = JSON.parse(JSON.stringify(this.form));
            payload.adderRows.forEach(adder => {
                if (adder.to === '' || adder.to === Infinity) {
                    adder.to = null;
                }
            });
            const obj = JSON.stringify(payload);
            debugger;
            var requestPayload = { objData: obj, ruleCondition: conditionData, conditionName: this.conditionName, configuration_id: vm.configurationId,sqlcondition:sqlDatabasecondition };
            await DataService.SaveAddRule(requestPayload).then(response => {
                if (response.data != null) {
                    debugger;
                    if (response.data.Status == "Success" && response.data.Result == "1") {
                        vm.Close();
                        if (vm.viewType == 'serviceRuleCondition') {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('BillingRule')]), "Success", true, vm.$t('Alert'));
                            vm.$parent.getConditionRecords();
                        }
                        if (vm.reqFrom == "fromBillingService" || vm.reqFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('BillingCondition')]), "Success", true, vm.$t('Alert'));
                            vm.$parent.FetchData();
                        }
                        vm.isLoading = false;
                        this.$emit('rule-saved', obj);
                    }
                    else if (response.data.Status == "Success" && response.data.Result == "3") {
                        vm.Close();
                        if (vm.reqFrom == "fromBillingService" || vm.reqFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('BillingCondition')]), "Success", true, vm.$t('Alert'));
                            vm.$parent.FetchData();
                        }
                        else{
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('BillingRule')]), "Success", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                        this.$emit('rule-saved', obj);
                    }
                    else if (response.data.Result == "2") {
                        if (vm.reqFrom == "fromBillingService" || vm.reqFrom == "addBillingService") {
                            this.ShowAlert(this.$t('AlreadyExists', [this.$t('BillingCondition')]), "failure", true, this.$t('Alert'));
                        }
                        else{
                            this.ShowAlert(this.$t('AlreadyExists', [this.$t('BillingRule')]), "failure", true, this.$t('Alert'));
                        }
                        
                    }
                    else {
                        if (vm.reqFrom == "fromBillingService" || vm.reqFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("BillingCondition")]), "danger", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                        else{
                            vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("BillingRule")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        }
                        
                    }
                }
            });
        },
        // New methods for the updated UI
        addFieldToCondition(field) {
            // Skip if it's a group header
            if (field.isGroup) return;
            
            // Add field to the first available condition in the first block
            if (this.conditionBlocks.length > 0 && this.conditionBlocks[0].conditions.length > 0) {
                const firstCondition = this.conditionBlocks[0].conditions[0];
                if (!firstCondition.field) {
                    firstCondition.field = field.value;
                    firstCondition.sqlType = field.sqlType;
                    this.handleFieldChange(firstCondition);
                } else {
                    // If first condition already has a field, add a new condition
                    this.addCondition(0);
                    const newCondition = this.conditionBlocks[0].conditions[this.conditionBlocks[0].conditions.length - 1];
                    newCondition.field = field.value;
                    newCondition.sqlType = field.sqlType;
                    this.handleFieldChange(newCondition);
                }
            }
        },
        toggleBlockOperator(blockIndex) {
            const block = this.conditionBlocks[blockIndex];
            block.operator = block.operator === 'AND' ? 'OR' : 'AND';
        },
        toggleJoinOperator(blockIndex) {
            // Toggle the operator that joins this block with the previous one
            const block = this.conditionBlocks[blockIndex];
            if (!block) return;
            block.joinWithPrevious = (block.joinWithPrevious === 'AND') ? 'OR' : 'AND';
        },
        removeBlock(blockIndex) {
            if (this.conditionBlocks.length > 1) {
                this.conditionBlocks.splice(blockIndex, 1);
            }
        },
        addCondition(blockIndex) {
            const block = this.conditionBlocks[blockIndex];
            block.conditions.push({
                field: '',
                operator: '',
                value: '',
                valueTo: '',
                daysOfWeek: '',
                operatorList: [],
                valueOptions: [],
                sqlType: ''
            });
        },
        removeCondition(blockIndex, conditionIndex) {
            const block = this.conditionBlocks[blockIndex];
            if (block.conditions.length > 1) {
                block.conditions.splice(conditionIndex, 1);
            }
        },
        addBlock() {
            this.conditionBlocks.push({
                operator: 'AND',
                joinWithPrevious: 'OR',
                conditions: [
                    { field: '', operator: '', value: '', operatorList: [] }
                ]
            });
        },
        // Sync conditionBlocks with form.conditions for backward compatibility
        syncConditionBlocks() {
            if (!(this.form.conditions && this.form.conditions.length > 0)) {
                return;
            }

            const blocks = [];
            let currentBlock = null;
            let pendingJoinWithPrevious = '';

            const pushCurrentBlockIfAny = () => {
                if (currentBlock && currentBlock.conditions.length > 0) {
                    // Decide intra-block operator based on relations recorded inside the block (after the first condition)
                    const intraRelations = (currentBlock._relations || [])
                        .filter(r => r && r.trim() !== '')
                        .map(r => r.toUpperCase());
                    if (intraRelations.length > 0) {
                        currentBlock.operator = intraRelations[0];
                    } else {
                        currentBlock.operator = currentBlock.operator || 'AND';
                    }
                    delete currentBlock._relations;
                    blocks.push(currentBlock);
                }
            };

            this.form.conditions.forEach((condition, index) => {
                const opening = (condition.openingBracket || '').toString();
                const closing = (condition.closingBracket || '').toString();
                const relation = (condition.relation || '').toString().toUpperCase();

                const hasOpening = opening.includes('(');
                const hasClosing = closing.includes(')');

                // Start a new block when we encounter an opening bracket or if no block exists yet
                if (hasOpening || !currentBlock) {
                    // Push previous block if it exists
                    pushCurrentBlockIfAny();

                    currentBlock = {
                        operator: 'AND',
                        joinWithPrevious: blocks.length > 0 ? (pendingJoinWithPrevious || 'OR') : '',
                        conditions: [],
                        _relations: []
                    };

                    // The relation on the first condition in a block represents the join with previous block
                    pendingJoinWithPrevious = relation || pendingJoinWithPrevious;
                }

                // Add current condition to the block
                currentBlock.conditions.push({
                    field: condition.field || '',
                    operator: condition.operator || '',
                    value: condition.value,
                    valueTo: condition.valueTo,
                    daysOfWeek: condition.daysOfWeek || '',
                    operatorList: condition.operatorList || [],
                    valueOptions: condition.valueOptions || [],
                    sqlType: condition.sqlType || ''
                });

                // Record intra-block relation (relation between previous and this condition)
                if (currentBlock.conditions.length > 1) {
                    currentBlock._relations.push(relation);
                }

                // Close the block when encountering a closing bracket
                if (hasClosing) {
                    // Assign the joinWithPrevious for this block using the first condition's relation stored earlier
                    currentBlock.joinWithPrevious = blocks.length > 0 ? (pendingJoinWithPrevious || 'OR') : '';
                    // After closing, reset pending join for the next block
                    pendingJoinWithPrevious = '';
                    pushCurrentBlockIfAny();
                    currentBlock = null;
                }
            });

            // In case the last block didn't explicitly close with a ')' but has conditions
            pushCurrentBlockIfAny();

            // Fallback: if nothing parsed into blocks, keep a single block
            if (blocks.length === 0) {
                this.conditionBlocks = [
                    {
                        operator: 'AND',
                        joinWithPrevious: '',
                        conditions: this.form.conditions.map(condition => ({
                            field: condition.field || '',
                            operator: condition.operator || '',
                            value: condition.value,
                            valueTo: condition.valueTo,
                            daysOfWeek: condition.daysOfWeek || '',
                            operatorList: condition.operatorList || [],
                            valueOptions: condition.valueOptions || [],
                            sqlType: condition.sqlType || ''
                        }))
                    }
                ];
            } else {
                this.conditionBlocks = blocks;
            }
        },
        CreateClone: function () {
            var vm = this;
            var cloneName = vm.$refs.validateClone.getPropertyValue("ServiceName", "value");
            var serviceId = vm.conditionId;
            var url = `serviceId=${serviceId}&serviceName=${cloneName}`;
            DataService.CloneBillingService(url).then((response) => {
                if (response.data.status == "Success") {
                    this.ShowAlert(this.$t('AddedSuccess', [this.$t('Service')]), "Success", true, this.$t('Alert'));
                    vm.$refs.validateClone.updateProperty('ServiceName', 'value', '');
                    vm.CloseCloneModel();
                    vm.CloseRuleModel();
                }
                else {
                    this.ShowAlert(this.$t('ErrorWhileSaving', [this.$t('Service')]), "danger", true, this.$t('Alert'));
                }
            });
        },
        // Sync form.conditions with conditionBlocks for saving
        syncFormConditions() {
            this.form.conditions = [];
            this.conditionBlocks.forEach((block, blockIndex) => {
                block.conditions.forEach((condition, conditionIndex) => {
                    if (
                        condition.field &&
                        condition.operator &&
                        (condition.value !== '' && condition.value !== null && condition.value !== undefined)
                    ) {
                        this.form.conditions.push({
                            relation: conditionIndex > 0 ? block.operator : (blockIndex > 0 ? (block.joinWithPrevious || 'OR') : ''),
                            openingBracket: blockIndex === 0 && conditionIndex === 0 ? '(' : '',
                            field: condition.field,
                            operator: condition.operator,
                            value: condition.value,
                            valueTo: condition.valueTo || '',
                            daysOfWeek: condition.daysOfWeek || '',
                            closingBracket: conditionIndex === block.conditions.length - 1 ? ')' : '',
                            operatorList: condition.operatorList || [],
                            valueOptions: condition.valueOptions || [],
                            sqlType: condition.sqlType || ''
                        });
                    }
                });
            });
        }
    },
};
</script>
